html, .body {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.flex-row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.title {
    font-weight: 700;
    font-size: 20px;
}
.button {
    display: inline-block;
    border-radius: 6px;
    width: 120px;
    height: 44px;
    font-size: 16px;
    text-align: center;
    line-height: 44px;
    cursor: pointer;
    border: 1px solid #ccc;
    font-weight: 500;
    color: #333;
    background: #fff;
}
.button:hover {
    border-color: #10c29b;
    color: #10c29b;
}
.button.green {
    background-color: #10c29b;
    color: #fff;
}
.button.green:hover {
    background-color: #61e8cf;
}

.button.disable {
    opacity: 0.5;
    cursor: default;
}
.button.disable:hover {
    color: #333;
    border-color: #ccc;
}

.page-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: #F6F6F6;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* display: none; */
    z-index: -1;
}
.page-wrapper.active {
    /* display: flex; */
    z-index: 1;
}
