.container { width: 100vw; height: 100vh; background-color: grey; }

.setting {
    position: absolute;
    padding: 20px;
    background-color: #FFFFFFDD;
    top: 0;
    right: 0;
    transition: all 0.3s;
    border-left: 1px solid #888;
        overflow-y: auto;
    height: calc(100vh - 40px);
}
.setting select {
    padding: 10px;
    font-size: 20px;
    margin-top: 10px;
    font-weight: 700;
}
.setting * {
    color:black;
}
.setting .set-title {
    margin-top: 20px;
    font-size: 18px;
    font-weight: 500;
}

.set-item {
    margin: 10px auto;
    width: 100%;
    display: flex;
    align-items: center;
}
/* .set-item.hide {
    display: none;
} */
.set-item select {
    width: 100%;
    padding: 10px;
}
.set-item label {
    width: 100px;
}
.set-item input {
    width: 20px;
    height: 20px;
}
